import groupBy from 'lodash/groupBy'
import upperFirst from 'lodash/upperFirst'
import React, { useMemo } from 'react'
import styled from 'styled-components'

import Box from 'components/Box'
import PageSimulator from 'containers/PageSimulator'
import PageTitle from 'containers/PageTitle'
import sortSize from 'utils/sortSize'

const StyledTable = styled.table``

const CompactPortfolio = ({
  name,
  colGroupKey,
  rowGroupKey,
  mainKey,
  timeKey,
  timeColor,
  data,
}) => {
  const rows = useMemo(() => {
    const flatData = data.reduce((all, d) => all.concat(d[1]), [])
    const rowGroup = groupBy(flatData, rowGroupKey)
    return sortSize(Object.keys(rowGroup))
  }, [data, rowGroupKey])

  console.log(data)
  console.log(rows)
  return (
    <PageSimulator name={`${name}-portfolio-table`}>
      <Box>
        <PageTitle>{`${upperFirst(name)} Memery Portfolio`}</PageTitle>
        <StyledTable position="absolute" left="86px" top="125px" right="86px">
          <thead>
          </thead>
        </StyledTable>
      </Box>
    </PageSimulator>
  )
}

export default CompactPortfolio
